/**
 * basic hover animation timing (of color and opacity).
 * timing and bezier should be refactored after official Porsche animation guidelines are defined
 */
.sg-example .sg-render {
  padding: 1.875rem;
  overflow-x: auto; }
  .sg-example .sg-render--light {
    background-color: #eff0f1; }
  .sg-example .sg-render--dark {
    background-color: #191f22; }

.sg-example .sg-toggle-button {
  padding: 0 0.375rem;
  position: relative;
  height: 30px;
  background: none;
  border: 1px solid #afb1b2;
  cursor: pointer;
  color: #000;
  font-family: "Porsche Next", "Arial Narrow", Arial, sans-serif;
  font-weight: 600;
  font-size: 0.875rem;
  line-height: 150%; }
  .sg-example .sg-toggle-button:focus {
    outline: 0; }
  .sg-example .sg-toggle-button--open {
    background-color: #fff; }

.sg-example .sg- code {
  padding: initial;
  background-color: initial;
  border-radius: initial; }

.sg-example .sg-codeblock {
  font-size: 16px;
  resize: vertical; }
  .sg-example .sg-codeblock pre {
    max-height: 200px;
    padding: 1.875rem !important;
    overflow: auto !important; }
  .sg-example .sg-codeblock .hljs-tag {
    line-height: 1.5; }
